export default function Dimmer(canvas) {
  const dimmerG = canvas.g().addClass('dimmer').attr({ visibility: 'hidden' });
  const createBackground = () => {
    dimmerG.rect(0, 0, 800, 500).addClass('f01 sno').attr({ opacity: 0.8 });
  };

  this.show = () => {
    dimmerG.attr('visibility', 'visible');
  };

  this.hide = () => {
    dimmerG.attr('visibility', 'hidden');
  };

  createBackground();
  canvas.before(dimmerG);

  return this;
}
