import Snap from 'sol-common/snap';

const D1Animate = {
    drawPath: ({ path, duration }) => {
        const totalLength = path.getTotalLength();
        const pathStr = path.attr('d');
        return new Promise(resolve => {
            Snap.animate(
                0,
                1,
                val => {
                    const subPath = Snap.path.getSubpath(pathStr, 0, val * totalLength);
                    path.attr({ d: subPath });
                },
                duration,
                undefined,
                resolve
            );
        });
    },
    moveTo: ({ dx, dy, duration, element, r = '' }) => {
        return new Promise(resolve => {
            Snap.animate(
                0,
                1,
                val => {
                    element.transform(`t ${dx * val} ${dy * val} ${r && 'r ' + r}`);
                },
                duration,
                undefined,
                resolve
            );
        });
    },

    onPath: ({ path, duration = 3000, element, local = '', easing }) => {
        const startPt = Snap.path.getPointAtLength(path, 0);
        return new Promise(resolve => {
            Snap.animate(
                0,
                1,
                val => {
                    const { x, y } = Snap.path.getPointAtLength(path, val * Snap.path.getTotalLength(path));
                    const transform = local + 't' + [x - startPt.x, y - startPt.y];
                    element.transform(transform);
                },
                duration,
                easing,
                resolve
            );
        });
    },
    blink: ({ interval = 0.5, duration = 500, element, opacity = 1 }) => {
        element.attr({ pointerEvents: 'none' });
        return new Promise(resolve => {
            Snap.animate(
                0,
                1,
                val => {
                    element.attr({ opacity: val });
                },
                duration,
                val => {
                    if (val < interval) return (-(1 / interval) * val + 1) * opacity;
                    else return (1 / (1 - interval)) * (val - interval) * opacity;
                },
                resolve
            );
        });
    },
    flip: ({ front, duration }) => {
        const { cx } = front.getBBox();
        return new Promise(resolve => {
            Snap.animate(0, 1, val => front.transform(Snap.matrix(1 - val, 0, 0, 1, val * cx, 0)), duration, undefined, resolve);
        });
    },
    sliding: ({ element, dx, dy, hiddenTime, duration }) => {
        const { local } = element.transform();
        return new Promise(resolve => {
            Snap.animate(
                0,
                1,
                val => {
                    const opacity = val < hiddenTime ? 1 : -(1 / (1 - hiddenTime)) * val + 1 / (1 - hiddenTime);
                    element.attr({ opacity }).transform(local + 't' + [dx * val * val, dy * val * val]);
                },
                duration,
                undefined,
                () => {
                    element.remove();
                    resolve();
                }
            );
        });
    },
    rolling: ({ element, directionAngle, duration, multiple, revolutions, easing = window.mina.linear, dxy }) => {
        const { local } = element.transform();
        const radius = element.getBBox().w / 2;
        const radian = directionAngle * (Math.PI / 180);
        const [x, y] = dxy
            ? [dxy.x / revolutions / multiple, dxy.y / revolutions / multiple]
            : [radius * Math.cos(radian) * Math.PI, -radius * Math.sin(radian) * Math.PI];
        const sign = Math.cos(radian) < 0 ? -1 : 1;
        const diretion = Math.abs(Math.sin(radian)) === 1 ? -Math.sin(radian) : sign;

        return new Promise(resolve => {
            Snap.animate(
                0,
                1,
                val => {
                    const translate = local + 't' + [revolutions * x * multiple * val, revolutions * y * multiple * val];
                    const rotate = 'r' + diretion * val * (revolutions * 360);
                    const scale = 's' + (1 + (multiple - 1) * val);
                    element.transform(translate + rotate + scale);
                },
                duration,
                easing,
                resolve
            );
        });
    },
    shake: ({ duration, rotate, element, reverse = true }) => {
        const sign = reverse ? 1 : -1;
        const { local } = element.transform();
        return new Promise(resolve =>
            Snap.animate(
                0,
                1,
                val => {
                    if (val < 0.25) val = 4 * val;
                    else if (val < 0.75) val = -4 * val + 2;
                    else val = 4 * val - 4;
                    element.transform(local + 'r' + sign * rotate * val);
                },
                duration,
                undefined,
                resolve
            )
        );
    },
    fadeIn: ({ element, duration, from = 0, to = 1 }) => {
        return new Promise(resolve => {
            const opacityNum = Number(element.attr('opacity'));
            Snap.animate(
                from,
                to,
                val => {
                    element.attr('opacity', opacityNum + (1 - opacityNum) * val);
                },
                duration,
                null,
                resolve
            );
        });
    },
    zoom: ({ type, element, duration, begin, end, local = '', from = 0, to = 1 }) => {
        return new Promise(resolve => {
            let scale = 0;
            !begin && (begin = type === 'in' ? 0 : 1);
            !end && (end = type === 'in' ? 1 : 0);

            Snap.animate(
                from,
                to,
                val => {
                    scale = type === 'in' ? begin + (1 - begin) * val + (end - 1) : 1 - val + (begin - 1) * (1 - val) + end * val;
                    element.transform(`${local}s ${scale}`);
                },
                duration,
                null,
                resolve
            );
        });
    }
};

export default D1Animate;
