import { loadLocalSound, loadFragment } from 'sol-common';
import Snap from 'sol-common/snap';
import { SOL } from 'sol-common/util';
import feedbackRight from '../assets/images/LI00000013.svg';
import feedbackWrong from '../assets/images/LI00000009.svg';
import LS00000301 from '../assets/sounds/LS00000301.mp3';
import LS00000300 from '../assets/sounds/LS00000300.mp3';

export default function D1feedback({ canvas = SOL.SVG.g(), el, bool, scale = 1, gap = [0, 0], rotation = 0, retouchTime = 300 }) {
    return new Promise(resolve => {
        const preventTouchRect = SOL.SVG.rect(0, 0, 800, 500).attr({ opacity: '0' }); // 추가 터치 방지
        const index = Number(bool);
        // 아이콘        
        const icon = [feedbackWrong, feedbackRight][index];
        const { node: iconNode } = loadFragment({ src: icon, key: 'feedback' + (index + 1), type: 'svg' });        
        const { x2, y } = el.getTBox();
        const iconSize = [iconNode.getAttribute('width'), iconNode.getAttribute('height')].map(el => el * scale);
        const trans = [x2 - iconSize[0] / 2 + gap[0], y - iconSize[1] + gap[1]];
        
        // 사운드
        const localSound = [LS00000301, LS00000300][index];
        const sound = loadLocalSound(localSound);

        // 아이콘 그리기
        canvas.transform('t' + trans + 'r' + rotation + 's' + scale);
        Snap(iconNode).appendTo(canvas);

        // setUserAction
        SOL.controller.addUserAction(index);

        const retouchTimeout = setTimeout(() => preventTouchRect.remove(), retouchTime);
        // 사운드 재생
        sound.play();
        sound.once('end', () => {
            preventTouchRect && preventTouchRect.remove();
            retouchTimeout && clearTimeout(retouchTimeout);
            canvas.remove();
            resolve();
        });
    });
}
