import Snap from 'sol-common/snap';

export const getSVGPoint = event => {
    const svg = document.querySelector('#frame2d');
    const pt = svg.createSVGPoint();
    pt.x = event.clientX;
    pt.y = event.clientY;
    return pt.matrixTransform(svg.getScreenCTM().inverse());
};
export const getUniquArray = arr => arr.filter((el, i) => arr.indexOf(el) === i);
export const get2dimArray = (arr, n) => {
    const newArray = [];
    arr.forEach((el, i) => {
        const q = Math.floor(i / n);
        const r = i % n;
        if (r === 0) newArray[q] = [];
        newArray[q][r] = el;
    });
    return newArray;
};
export const getTransposeArray = arr => {
    const newArray = [];
    for (let i = 0; i < arr[0].length; i++) {
        const tempArr = arr.map(el => el[i]);
        newArray.push(tempArr);
    }
    return newArray;
};

export const getClassNames = element => element.node.className.baseVal;

export const getPointInMatrixForm = ({ startPoint = { x: 0, y: 0 }, rows, columns, gap = { x: 0, y: 0 }, reverse = false, center = true }) => {
    const point = [];
    const offset = { x: center ? -gap.x * 0.5 * (columns - 1) : 0, y: center ? -gap.y * 0.5 * (rows - 1) : 0 };

    for (let i = 0; i < rows; i++) {
        for (let j = 0; j < columns; j++) {
            const index = i * columns + j;
            const [x, y] = [startPoint.x + gap.x * j + offset.x, startPoint.y + offset.y + (reverse ? -gap.y * i : gap.y * i)];
            point[index] = { x, y };
        }
    }
    return point;
};
export const getPointInScatterForm = ({ area, pointNum, neighborhood }) => {
    const points = [];
    const isPointInsideBBox = (bbox, x, y) => Math.abs(bbox.cx - x) <= bbox.width / 2 && Math.abs(bbox.cy - y) <= bbox.height / 2;
    const isBBoxIntersect = (bbox1, bbox2) =>
        isPointInsideBBox(bbox2, bbox1.x, bbox1.y) ||
        isPointInsideBBox(bbox2, bbox1.x2, bbox1.y) ||
        isPointInsideBBox(bbox2, bbox1.x, bbox1.y2) ||
        isPointInsideBBox(bbox2, bbox1.x2, bbox1.y2) ||
        isPointInsideBBox(bbox1, bbox2.x, bbox2.y) ||
        isPointInsideBBox(bbox1, bbox2.x2, bbox2.y) ||
        isPointInsideBBox(bbox1, bbox2.x, bbox2.y2) ||
        isPointInsideBBox(bbox1, bbox2.x2, bbox2.y2);

    const limit = 1000;
    let n = 0;

    for (let index = 0; index < pointNum; index++) {
        let repeat = true;
        while (repeat && n < limit) {
            const cx = getRandomNumber(area.x + neighborhood.width / 2, area.x + area.width - neighborhood.width / 2);
            const cy = getRandomNumber(area.y + neighborhood.height / 2, area.y + area.height - neighborhood.height / 2);
            const boundingBox = {
                x: cx - neighborhood.width / 2,
                x2: cx + neighborhood.width / 2,
                cx,
                y: cy - neighborhood.height / 2,
                y2: cy + neighborhood.height / 2,
                cy,
                width: neighborhood.width,
                height: neighborhood.height
            };
            const isOverlap = points.some(el => isBBoxIntersect(el, boundingBox));
            if (!isOverlap) {
                points.push(boundingBox);
                repeat = false;
            }
            n += 1;
        }
    }

    if (n === limit) console.error('CAN NOT GENERATE!!!');
    return points;
};

export const getCloseElement = (targetElement, elements) =>
    elements.reduce((a, b) => {
        const currentPoint = targetElement.getBBox().cx;
        const beforeDx = Math.abs(a.getBBox().cx - currentPoint);
        const currentDx = Math.abs(b.getBBox().cx - currentPoint);
        return beforeDx > currentDx ? b : a;
    }, elements[0]);

export const wait = (delay = 300) => new Promise(resolve => setTimeout(resolve, delay));

export const removeArrayElement = (arr, element) => arr.filter(el => el !== element);
export const getRandomArrayElement = (arr, n) => {
    const newArray = shuffle(arr).slice(0, n);
    while (newArray.length < n) {
        newArray.push(shuffle(arr)[0]);
    }
    return newArray;
};
export const getRandomNumber = (min, max) => Math.floor(Math.random() * (max - min + 1)) + min;
export const getRandomNumberArray = (min, max, n, isUnique = true) => {
    let randArr = [];
    let uniqueArr = [];
    if (isUnique) {
        while (uniqueArr.length !== n) {
            for (let index = 0; index < n; index++) {
                randArr[index] = getRandomNumber(min, max);
            }
            uniqueArr = getUniquArray(randArr);
        }
    } else {
        for (let index = 0; index < n; index++) {
            randArr[index] = getRandomNumber(min, max);
        }
    }

    return randArr;
};
export const getSumArray = (arr) => arr.reduce((acc, num) => acc + num);

export const getSimultaneousEquationConstant = ([x1, y1], [x2, y2]) => {
    const a = (y2 - y1) / (x2 - x1);
    const b = y2 - a * x2;
    return { a, b };
};

export const convertToPathStr = {
    line: ({ x1 = 0, y1 = 0, x2 = 0, y2 = 0 }) => 'M' + [x1, y1] + ' L' + [x2, y2],
    rect: ({ x = 0, y = 0, width = 0, height = 0 }) => 'M' + [x, y] + ' l' + [0, height] + ' l' + [width, 0] + ' l' + [0, -height] + ' l' + [-width, 0],
    circle: ({ cx = 0, cy = 0, r = 0 }) => 'M' + [cx + r, cy - r] + 'm' + [-r, 0] + 'a' + [r, r, 0, 1, 1, 0, r * 2] + 'a' + [r, r, 0, 1, 1, 0, -(r * 2)],
    polygon: (..._rest) => {
        const rest = Array.isArray(_rest[0]) ? _rest[0] : _rest;
        return rest.reduce((a, b, i) => {
            if (i === 0) return (a += b + ',');
            else if (i === 1) return (a += b + ' ');
            else if (i % 2 === 0) return (a += 'L' + b + ',');
            else return (a += b + ' ');
        }, 'M');
    }
};

export const shuffle = array => {
    const copyArray = array.map(el => el);
    for (let i = copyArray.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        const k = copyArray[i];
        copyArray[i] = copyArray[j];
        copyArray[j] = k;
    }
    return copyArray;
};

export const makeArrayNumber = ({ startNum = 1, endNum }) => {
    const rangeNum = [];
    for (let i = startNum; i < endNum + 1; i++) {
        rangeNum.push(i);
    }
    return rangeNum;
};

export const isSameArray = (a, b) => {
    let n;
    if (a.length === b.length) {
        n = 0;
        a.forEach((el, i) => {
            if (el === b[i]) n += 1;
        });
    }
    return n === a.length;
};

export const saveImage = svg => {
    const canvas = document.createElement('canvas');
    canvas.width = 800;
    canvas.height = 800;
    const data = new XMLSerializer().serializeToString(svg);
    const blob = new Blob([data], { type: 'image/svg+xml' });
    const img = new Image();
    const url = URL.createObjectURL(blob);
    img.src = url;

    img.onload = () => {
        canvas.width = img.width;
        canvas.height = img.height;
        canvas.getContext('2d')?.drawImage(img, 0, 0);
        URL.revokeObjectURL(url);
        const uri = canvas.toDataURL('image/png').replace('image/png', 'octet/stream');
        console.log(uri);
        const a = document.createElement('a');
        a.href = uri;
        a.download = 'myPhoto.png';
        a.click();
        URL.revokeObjectURL(uri);
    };
};

export const calculateAnswer = (num, sign) => {
    let ans;
    switch (sign) {
        case '+':
            ans = 0;
            num.forEach(el => {
                ans = ans + el;
            });
            break;
        case '-':
            num.forEach((el, i) => {
                ans = i === 0 ? el : ans + el;
            });
            break;
        case '*':
            ans = 1;
            num.forEach(el => {
                ans = ans * el;
            });
            break;
        case '/':
            ans = {};
            ans.q = Math.floor(num[0] / num[1]);
            ans.r = num[0] % num[1];
            break;
    }
    return ans;
};
