import Snap from 'sol-common/snap';
import D1Animate from '../utils/D1Animate';
import { getRandomNumber } from '../utils/util';

export default function D1drag({ dragItem, draggingItem, startCallback, moveCallback, endCallback }) {
    dragItem = dragItem.addClass('cp');
    draggingItem && draggingItem.attr({ opacity: '0', pointerEvents: 'none' });

    const originPoint = dragItem.getBBox();
    const dragStart = element => element.data('originTransform', element.transform().local);
    const dragMove = (elemenet, dx, dy) => {
        const origin = elemenet.data('originTransform') || '';
        const snapInvMatrix = elemenet.transform().diffMatrix.invert();
        dx = snapInvMatrix.x(dx, dy);
        dy = snapInvMatrix.y(dx, dy);
        elemenet.transform(origin + 't' + [dx, dy]);
    };
    const dragEnd = element => element.data('originTransform', element.transform().local);

    const start = () => {
        dragStart(dragItem);
        if (draggingItem) {
            setDragState(false);
            dragStart(draggingItem);
        }
        if (startCallback) startCallback();
    };
    const move = (dx, dy) => {
        dragMove(dragItem, dx, dy);
        if (draggingItem) {
            setDragState(false);
            dragMove(draggingItem, dx, dy);
        }
        if (moveCallback) moveCallback();
    };
    const end = () => {
        dragEnd(dragItem);
        if (draggingItem) {
            setDragState(true);
            dragEnd(draggingItem);
        }
        if (endCallback) endCallback();
    };

    const setDragState = bool => {
        dragItem.attr({ opacity: Number(bool) });
        draggingItem.attr({ opacity: Number(!bool) });
    };

    const animate = async ({ pathStr, easing, randomDuration: _randomDuration }) => {
        const { local } = dragItem.transform();
        dragItem.attr({ pointerEvents: 'none' });
        const randomDuration = _randomDuration ? getRandomNumber(800, 1100) : 500;
        if (draggingItem) {
            D1Animate.onPath({ path: pathStr, duration: randomDuration, element: dragItem, local, easing });
            await D1Animate.onPath({ path: pathStr, duration: randomDuration, element: draggingItem, local, easing });
        } else {
            await D1Animate.onPath({ path: pathStr, duration: randomDuration, element: dragItem, local, easing });
        }
        dragItem.attr({ pointerEvents: 'auto' });
    };

    dragItem.spectrumEffect = ({ interval: _interval = 100, duration = 750 }) => {
        const interval = setInterval(() => {
            const clone = dragItem.clone();
            clone.after(dragItem);
            Snap.animate(
                1,
                0,
                val => clone.attr({ opacity: val }),
                duration,
                undefined,
                () => clone.remove()
            );
        }, _interval);

        const remove = () => clearInterval(interval);

        return { remove };
    };

    dragItem.moveAnimate = async (point, easing, randomDuration) => {
        const { x, y } = dragItem.getBBox();
        const pathStr = 'M' + [x, y] + 'L' + [point.x, point.y];
        await animate({ pathStr, easing, randomDuration });
    };
    dragItem.reset = async (dx = 0, dy = 0) => {
        const { x, y } = dragItem.getBBox();
        const pathStr = 'M' + [x, y] + 'L' + [originPoint.x + dx, originPoint.y + dy];
        await animate({ pathStr });
    };
    dragItem.moveAnimateWithScale = ({ dx, dy, scale }) => {
        const { local } = dragItem.transform();
        return new Promise(resolve => {
            Snap.animate(
                0,
                1,
                val => {
                    const move = 't' + [val * dx, val * dy];
                    const _scale = 's' + (1 - ((scale.from - scale.to) / scale.from) * val);
                    dragItem.transform(local + move + _scale);
                },
                750,
                undefined,
                resolve
            );
        });
    };
    dragItem.hidden = () => dragItem.attr({ opacity: '0', pointerEvents: 'none' });
    dragItem.drag(move, start, end);

    return dragItem;
}
