import { loadImage } from 'sol-common';
import Snap from 'sol-common/snap';
import { convertToPathStr } from '../utils/util';
import Dimmer from './D1dimmer';
import timerBg from '../assets/images/D1timer.svg';

export default function Timer(canvas, options) {
    const timerData = {
        background: {
            image: {
                src: timerBg,
                x: 735,
                y: 10,
                scale: 1,
            },
        },
        numberAttrs: {
            fontSize: 24,
            color: '#1C233C',
            offset: { x: 0, y: -3 },
            className: '',
        },
        progress: {
            strokeWidth: 3,
            className: 's02',
            offset: { x: -0.5, y: 2.5 },
        },
        timeout: 60 || 999, // TODO: 제한 시간 데어터 연동 추가
        ...options,
    };
    const {
        background,
        numberAttrs,
        progress,
        timeout,
        beforeStopCb,
        afterStopCb,
    } = timerData;

    let seconds = 0;
    let timerInterval = null;
    let isPlaying = false;
    let progressPath = null;
    let pathTotalLength = 0;
    let dimmer = null;

    const animateTimeout = () => {
        const timerGBBox = this.timerG.getBBox();
        const duration = 1000;
        const cx = 400;
        const cy = 250;
        const scale = 2;

        const animateRotation = (cb) => {
            const local = this.timerG.transform().local;
            Snap.animate(
                0,
                100,
                (val) => {
                    const t = Math.floor(val / 10) % 2 ? 5 : -5;
                    this.timerG.transform(`${local} t${t} `);
                },
                600,
                null,
                () => {
                    this.timerG.transform(`${local} t0 `);
                    cb();
                }
            );
        };

        return new Promise((resolve) => {
            Snap.animate(
                0,
                1,
                (val) => {
                    this.timerG.transform(
                        `t ${(cx - timerGBBox.cx) * val} ${
                            (cy - timerGBBox.cy) * val
                        } s ${scale * val + 1}`
                    );
                },
                duration,
                null,
                () => {
                    animateRotation(resolve);
                }
            );
        });
    };

    const startInterval = () => {
        const textEl = this.timerG.select('.timer-text');
        const secondTextEl = textEl.select('.number');

        timerInterval = setInterval(() => {
            if (seconds >= timeout) {
                this.stop();
                return;
            }

            seconds++;
            secondTextEl.attr({ text: seconds });

            if (timeout !== 999) {
                const lengthPerTime = pathTotalLength / timeout;
                Snap.animate(
                    0,
                    1,
                    (val) => {
                        progressPath.attr({
                            strokeDashoffset:
                                pathTotalLength -
                                lengthPerTime * (seconds - 1) -
                                lengthPerTime * val,
                        });
                    },
                    1000,
                    null,
                    null
                );
            }
        }, 1000);
    };

    this.timerG = canvas.g().addClass('timer');
    this.endTime = 0;

    this.destroy = () => {
        isPlaying = false;
        clearInterval(timerInterval);
        this.timerG.remove();
    };

    this.reset = () => {
        seconds = 0;
        this.timerG.select('.number').attr({ text: seconds });
        progressPath.attr({
            strokeDashoffset: pathTotalLength,
            strokeDasharray: pathTotalLength,
        });
    };

    this.pause = () => {
        // TODO: 문제를 다 푼 경우: 시간 state 설정
        if (isPlaying) {
            clearInterval(timerInterval);
        } else {
            startInterval();
        }
        isPlaying = !isPlaying;
    };

    this.stop = async () => {
        isPlaying = false;
        clearInterval(timerInterval);
        dimmer.show();
        beforeStopCb && beforeStopCb();
        await animateTimeout();
        afterStopCb && afterStopCb();
    };

    this.start = () => {
        isPlaying = true;
        startInterval();
    };

    this.init = () => {
        const createElement = () => {
            const backgroundG = this.timerG.g().addClass('timer-background');
            const { image: bgImg } = background;
            const imgBg = loadImage({
                src: timerBg,
                key: 'timerBG',
                type: 'svg',
            })
                .use()
                .attr({
                    transform: `t ${bgImg.x} ${bgImg.y} s ${bgImg.scale}`,
                });
            backgroundG.append(imgBg);
            const backgroundBBox = backgroundG.getBBox();

            const timerTextG = this.timerG.g().addClass('timer-text');
            timerTextG
                .text(
                    0 + numberAttrs.offset.x,
                    0 + numberAttrs.offset.y,
                    seconds.toString()
                )
                .addClass(`number ${numberAttrs.className}`)
                .attr({
                    fontSize: numberAttrs.fontSize,
                    fill: numberAttrs.color,
                    textAnchor: 'middle',
                });
            const textGBBox = timerTextG.getBBox();
            timerTextG.transform(
                `t ${backgroundBBox.cx} ${backgroundBBox.cy + textGBBox.h / 2}`
            );

            const progressG = this.timerG.g().addClass('timer-progress');
            const progressPathStr = convertToPathStr.circle({
                cx: backgroundBBox.cx + progress.offset.x,
                cy: backgroundBBox.cy + progress.offset.y,
                r: backgroundBBox.w / 2 - 7,
            });
            const { className: strokeClass, strokeWidth } = progress;

            pathTotalLength = Snap.path.getTotalLength(progressPathStr);
            progressPath = progressG.path(progressPathStr).attr({
                class: `${strokeClass} fno`,
                strokeDashoffset: timeout !== 999 ? pathTotalLength : 0,
                strokeDasharray: pathTotalLength,
                strokeWidth: strokeWidth,
            });

            dimmer = new Dimmer(this.timerG);
        };

        createElement();
        this.start();
    };

    return this;
}
